.contact-info {
  display: block;
  font-size: 16px;
  line-height: 1.25;
  text-transform: capitalize;
  color: #2b2b2b;
  margin-bottom: 8px;
  @include desktop{
    margin-bottom: 18px;
  }}
.contact-part {
  margin-bottom: 24px;
  @include desktop{
    margin-bottom: 68px;
  }
}
.contacts {
  padding-bottom: 60px;
  @include desktop{
    padding-bottom: 80px;
  }
}
.weekday-work {
  margin-bottom: 8px;
}
.contacts-pg-down {
  @include tablet {
    display: flex;
    column-gap: 26px;
  }
  @include desktop {
    display: none;
  }
}
.contact-part.info {
  @include tablet {
    text-align: right;
  }
}
.contacts-pg-down-item {
  margin-left: auto;
  }

.contacts-title {
  font-weight: 700;
  font-size: 18px;
  line-height: 1.28;
  color: #487996;
  text-transform: uppercase;
  margin-bottom: 8px;
  @include desktop{
    margin-bottom: 18px;
    font-size: 36px;
line-height: 45px;
  }
}
.contacts-title.desk{
  margin-bottom: 16px;
}
.contacts-title.small-text{
  text-transform: none ;
}
.wrap-form-contact{
  display: none;
  @include desktop{
    display: flex;
    justify-content: space-between;
    flex-direction: row-reverse;
  }
}
